@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700,700i&display=swap);
:root{--color-white: #fff;--color-yellow: gold;--color-purple: #639;--color-gray: #999;--color-gray-dark: #040404;--color-gray-light: #ccc;--color-link: #639;--color-link-hover: #fff;--color-foreground: #999;--color-background: gold;--color-background-hero: #040404;--color-background-footer: #040404;--color-background-nav-sticky: #639;--common-transition-time: 0.5s;--font-family: 'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif}html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{margin:0;font-size:1.15rem;font-family:'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;font-family:var(--font-family);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a:not(.button){text-decoration:none;color:#639;color:var(--color-link);-webkit-transition:color 0.5s,padding 0.5s;transition:color 0.5s,padding 0.5s;-webkit-transition:color var(--common-transition-time),padding var(--common-transition-time);transition:color var(--common-transition-time),padding var(--common-transition-time)}a:not(.button):hover{padding-left:0.2rem;padding-right:0.2rem;border-radius:0.5rem;color:#fff;color:var(--color-link-hover);background-color:#639;background-color:var(--color-link)}h1,h2,h3,h4,h5,h6{line-height:2;font-weight:400}h1{font-size:2rem}p,li{line-height:2}

